import React from "react";
import { Modal, Form, Input, Select, Button, InputNumber } from "antd";
import { StopOutlined, PlusOutlined } from "@ant-design/icons";
import CurrencyFormat from 'react-currency-format'
import { createBuyer } from "../../../api/buyer";
import { notifyScreen } from "../../../utils/notify";
import "../styles/ModalAddAndUpdate.scss"

const ModalAddBuyer = ({ hideModal, handleHideModal }) => {
  const handlerFormAdd = async (values) => {
    try {
      let code = values.code || "";
      let name = values.name || "";
      let age = values.age || "";
      let sex = values.sex || "";
      let phone = values.phone || "";
      let address = values.address || "";
      let note = values.note || "";
      let buyer = { code, name, phone, address, note, sex, age };
      let res = await createBuyer(buyer);
      if (res.status === 200) {
        notifyScreen("success", res.data.statusCode, res.data.message);
        return handleHideModal();
      }
    } catch (error) {
      if (error.response) {
        if (error.response.data) {
          notifyScreen(
            "error",
            error.response.data.statusCode,
            error.response.data.message
          );
        }
      } else {
        notifyScreen("error", "500", "Li khng xc nh");
      }
    }
  };

  const checkDayBirth = (_, value) => {
    if (value) {
      let values = value.split("/")
      if (values.length<3) {
       return Promise.reject(new Error("Vui lng nhp ng nh dng DD/MM/YYYY !"));
      }else{
        if (parseInt(values[0])> 31) {
          return Promise.reject(new Error("Ngy sinh khng ng !"));
        }else{
          if (parseInt(values[1])> 12) {
            return Promise.reject(new Error("Thng sinh khng ng !"));
          }else{
            if (parseInt(values[2]) > 2020) {
              return Promise.reject(new Error("Nm sinh khng ng !"));
            }else{
              return Promise.resolve();
            }
          }
        }
      }
     
    }

    return Promise.reject(new Error("Vui lng nhp ngy sinh !"));
  };

  return (
    <Modal
      visible={hideModal}
      onCancel={handleHideModal}
      title="Thm khch hng"
      footer={null}
      width={800}
    >
      <Form onFinish={handlerFormAdd} className="form-add-buyer">
        <div className="wrapper-input-form">
          <div>
            <div>
              <span style={{width:"100px"}}>M KH</span>
              <Form.Item
                name="code"
                rules={[{ required: true, message: "Vui lng nhp m KH!" }]}
              >
                <Input placeholder="Nhp m KH - VD : 0001" bordered={false}/>
              </Form.Item>
            </div>
            <div >
              <span style={{width:"100px"}}>Tn KH</span>
              <Form.Item
                name="name"
                rules={[{ required: true, message: "Vui lng nhp tn!" }]}
              >
                <Input placeholder="Nhp tn KH" bordered={false}/>
              </Form.Item>
            </div>
            <div>
              <span style={{width:"100px"}}>Ngy sinh</span>
              <Form.Item name="age" rules={[{ validator: checkDayBirth }]}>
                <CurrencyFormat format="##/##/####" placeholder="DD/MM/YYYY" mask={['D','D','M', 'M', 'Y', 'Y','Y','Y']} />
              </Form.Item>
            </div>
          </div>
          <div>
            <div>
              <span style={{width:"100px"}}>Gii tnh</span>
              <Form.Item
                name="sex"
                rules={[
                  { required: true, message: "Vui lng chn gii tnh!" },
                ]}
              >
                <Select placeholder="--La chn--" bordered={false}>
                  <Select.Option key="1" value="male">
                    Nam
                  </Select.Option>
                  <Select.Option key="2" value="female">
                    N
                  </Select.Option>
                </Select>
              </Form.Item>
            </div>
            <div>
              <span style={{width:"100px"}}>S T</span>
              <Form.Item name="phone" rules={[{ required: true, message: "Vui lng nhp ST!" }]}>
                <CurrencyFormat format="####-###-##########" placeholder="Nhp ST"/>
              </Form.Item>
            </div>
            <div>
              <span style={{width:"100px"}}>a ch</span>
              <Form.Item name="address" rules={[{ required: true, message: "Vui lng nhp a ch!" }]}>
                <Input placeholder="Nhp a ch" bordered={false}/>
              </Form.Item>
            </div>
          </div>
         
        </div>
        <div className="note">
            <span style={{width:"100px"}}>Ghi ch</span>
            <Form.Item name="note">
              <Input.TextArea placeholder="Nhp ghi ch" autoSize={{ minRows: 3}}/>
            </Form.Item>
          </div>
        <div className="btn-action-modal-add">
          <Form.Item>
            <Button
              type="primary"
              htmlType="submit"
              icon={<PlusOutlined />}
              className="btn-submit-form-add"
            >
              Thm
            </Button>
          </Form.Item>
          <Form.Item>
            <Button
              type="primary"
              onClick={handleHideModal}
              icon={<StopOutlined />}
              className="btn-close-form-add"
            >
              Hy b
            </Button>
          </Form.Item>
        </div>
      </Form>
    </Modal>
  );
};

export default ModalAddBuyer;
