import { useState, useEffect } from "react";
import { Modal, Table, InputNumber, Input, Button } from "antd";
import {CreditCardOutlined,StopOutlined } from "@ant-design/icons";
import "../styles/ModalAdjust.scss";

const ModalPaidDebt = ({ listBill, hideModal, handleHideModal }) => {
  const [dayCreateBill, setDayCreateBill] = useState("");
  const [hourCreateBill, setHourCreateBill] = useState("");


  const __getTime = () => {
    let date = new Date();
    let hour = date.getHours() + " : " + date.getMinutes();
    let day =
      date.getDate() + "/" + (date.getMonth() + 1) + "/" + date.getFullYear();
    setHourCreateBill(hour);
    return setDayCreateBill(day);
  };

  const columns = [
    {
      title: "M ha n",
      dataIndex: "code",
      key: "code",
      render: (text) => {  
          return "HD0000" + text
      }
    },
    {
      title: "Thi gian",
      dataIndex: "createdDay",
      key: "createdDay",
    },
    {
      title: "Gi",
      dataIndex: "createdHour",
      key: "createdHour",
    },
    {
      title: "Gi tr ha n",
      key: "totalBuyerPaidNeed",
      dataIndex: "totalBuyerPaidNeed",
    },
    {
      title: " thu trc",
      key: "debtRedundancy",
      dataIndex: "debtRedundancy",
    },
    {
        title: "Cn cn thu",
        key: "debtRedundancy",
        dataIndex: "debtRedundancy",
    },
    {
        title: "Tin thu",
        key: "moneypaid",
        render:()=>{
            return <InputNumber/>
        }
      }
     
  ];

  useEffect(()=>{
      __getTime()
  })

  return (
    <Modal
      centered
      title="Thanh ton"
      visible={hideModal}
      onCancel={handleHideModal}
      width={1000}
      footer={null}
    >
      <div className="top-paid-modal">
        <div>
          <div>
            <span>N hin ti</span>
            <span>10000</span>
          </div>

          <div>
            <span>Thu t khch</span>
            <InputNumber className="input-money" bordered={false} />
          </div>

          <div>
            <span>N sau</span>
            <span>10000</span>
          </div>
        </div>
        <div>
          <span>Thi gian</span>
          <div>
            <span>{dayCreateBill}</span>
            <span>{hourCreateBill}</span>
          </div>
        </div>
        <div>
           <Input.TextArea placeholder="Ghi ch"/>
        </div>
      </div>
      <div className="middle-paid-modal">
          <Table dataSource={[]} columns={columns}/>
          <div className="total-money">
              <div>
                  <span>Tng thanh ton ha n</span>
                  <span>0</span>
              </div>
              <div>
                  <span>Cng vo ti khon khch</span>
                  <span>0</span>
              </div>
          </div>
      </div>
      <div className="bottom-paid-modal">
          <Button icon={<CreditCardOutlined/>} type="primary">To phiu thu</Button>
          <Button icon={<CreditCardOutlined/>} type="primary">To phiu thu v in</Button>
          <Button icon={<StopOutlined />} type="primary">B qua</Button>
      </div>
    </Modal>
  );
};

export default ModalPaidDebt;
